.BlogImage {
  border-radius: 0.5rem !important;
  object-fit: fill;
  border: 1px solid #ccc;
}
.blogCard {
  box-shadow: 0.2rem 0.3rem 0.4rem rgba(0, 0, 0, 0.3);
  background: #eee;
  border: 1px solid #ccc;
}
.description {
  height: 10rem;
}
.description::-webkit-scrollbar {
  display: none;
}

/* Header */
.Logo {
  text-decoration: none;
  color: #fff;
  font-weight: bold;
}

@media (max-width: 600px) {
  .blogCard {
    width: 100% !important;
  }
  .BlogImage {
    height: 280px;
  }
  .Logo {
    font-weight: 700;
    font-size: 1.7rem;
  }
}

@media (max-width: 450px) {
  .Logo {
    font-weight: 600;
    font-size: 1.6rem;
  }
}
