.blogs {
  width: 100%;
  padding: 2rem 3rem;
  display: flex;
  flex-wrap: wrap;
  gap: 1rem;
}
.Userblogs {
  width: 100%;
  height: 100vh;
  padding: 2rem 3rem;
  display: flex;
  flex-wrap: wrap;
  gap: 1rem;
  align-items: center;
  justify-content: center;
  margin: 2rem 0;
}
.Userblogs h1 {
  padding: 2rem;
  border-radius: 0.5rem;
  background: rgba(0, 0, 0, 0.7);
  color: #fff;
  text-transform: capitalize;
  border: 2px solid rgb(0, 255, 0);
}
/* Create Blogs*/
.createBlog {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100vh;
  background: linear-gradient(to right top, #a33d5e, #ac37e2);
}
.CBox {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  width: 40rem;
  background: rgba(0, 0, 0, 0.4);
  color: #fff;
  border-radius: 0.5rem;
  border: 2px solid #999;
  padding: 1rem 1.5rem;
  margin-top: -4rem;
  box-shadow: 0.2rem 0.3rem 0.4rem rgba(0, 0, 0, 0.4);
}

/* -------------------------Blog Details------------------------- */

.blogDetails {
  width: 100%;
  min-height: 100vh;
  background: rgb(162, 0, 255);
  padding: 2rem 2rem;
  display: flex;
  align-items: center;
  flex-direction: column;
}
.blogDetails > h1 {
  font-size: 2.5rem;
  font-weight: bold;
  color: #fff;
  text-shadow: 0.3rem 0.3rem 0.3rem rgba(0, 0, 0, 0.7);
}
.containerd {
  width: 60%;
  padding: 2rem 1.5rem;
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
  background: rgba(0, 0, 0, 0.3);
  border-radius: 1rem;
  box-shadow: 0.3rem 0.3rem 0.4rem rgba(0, 0, 0, 0.4);
  margin-top: 2rem;
}
.containerd > .image {
  width: 100%;
  border-radius: 0.5rem;
  height: 20rem;
  overflow: hidden;
  box-shadow: 0.3rem 0.3rem 0.4rem rgba(0, 0, 0, 0.4);
}
.containerd > .image > img {
  width: 100%;
  height: 100%;
  object-fit: fill;
}
.containerd > .details {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  color: #fff;
}
.containerd > .details > h3 {
  font-size: 1.4rem;
  font-weight: 700;
  color: #111;
}
.containerd > .details > h3 > span {
  color: #fff;
}
.containerd > .details > p {
  font-size: 1rem;
  font-weight: 500;
  color: #ccc;
  text-align: justify;
  line-height: 30px;
}
/* -----------------------Responsve -----------------------*/
@media (max-width: 800px) {
  .blogs {
    padding: 2rem 2rem;
  }
  .containerd {
    width: 90%;
    padding: 2rem 1.5rem;
  }
}

@media (max-width: 700px) {
  .blogs {
    padding: 2rem 2rem;
  }
  .CBox {
    width: 30rem;
  }
}

@media (max-width: 500px) {
  .blogs {
    padding: 2rem 1rem;
  }
  .CBox {
    width: 22rem;
    margin-top: 0rem;
  }
  .Userblogs {
    padding: 2rem 1rem;
  }
  .blogDetails {
    padding: 1.5rem 1rem;
  }
  .containerd {
    width: 100%;
    padding: 0.6rem 0.5rem;
  }
}
